.main-content {
    display: block;
    position: absolute;
    margin: 5px 10%;
    width: 80%;
    top: 75%;
}

.main-content-enter {
    display: block;
    position: absolute;
    margin: 5px 10%;
    top: 75%;
}

.main-content-enter-active {
    top: 10%;
    transition: top 1s; 
}

.main-content-enter-done {
    top: 10%;
    transition: top 1s;
}