#HistoryList {
    display: flex;
    justify-content: center;
    position: fixed;
    bottom: 0%;
    padding: 0% 10%;
    width: 80%;
    height: 25%;
    background-color: var(--theme_black);
    box-sizing: revert;
}

#HistoryList div {
    margin: auto 0px;
    width: 100%;
}

#HistoryList h3 {
    margin: auto 0px;
    color: whitesmoke;
}

#HistoryList .MovieElement {
    color: whitesmoke;
}

#HistoryList ul {
    overflow:hidden;
    white-space: nowrap;
    padding: 0px;
    margin: auto 0px;
    display: grid;
    grid-template-columns: repeat(5, 20%);
}

#HistoryList ul li {
    display: inline-block;
    margin: 0px;
}

.history-list-btn{
    padding: 0px;
    width: 112px;
    height: 158px;
}

.history-list-btn img{
    width: 100%;
    height: 100%;
}