body {
    margin: 0px;
    overflow-x: hidden;
}

#main {
    margin: 0;
    width: 100vw;
    height: 100vh;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    --theme_black: #121212;
    --theme_grey: #dadada;
    --theme_yellow: #F5C518;
    background-color: var(--theme_grey);
}

h2 {
    margin: auto;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.btn {
    margin:0px 5px;
}

#nomination-list-container{
    height: calc(60vh - 5px);
}

#NominationList {
    display: flex;
    height: 90%;
    justify-content: center;
}

#NominationList #bg{
    height: 100%;
    object-fit: scale-down;
}

#preview-container{
    position: relative;
    height: 100%;
}

.nomination-elements-container{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
}

.nomination-list-text p{
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    margin: auto;
}

.nomination-list-btn img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.page-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 10%;
}

.page-header h2{
    margin: 0px;
}

